import React, { Component } from 'react';
import './SideNav.css'
import './w3.css'
import logo from '../assets/logo.png'
import { Link, withRouter } from 'react-router-dom'

class SideNav extends Component {
    // openNav = () => {
    //     document.getElementById("mySidenav").style.width = "250px";
    // }
    // closeNav = () => {
    //     document.getElementById("mySidenav").style.width = "0";
    // }

    w3_open = () => {
        document.getElementsByClassName("w3-sidenav")[0].style.display = "block";
    }
    w3_close = () => {
        document.getElementsByClassName("w3-sidenav")[0].style.display = "none";
    }

    render() {
        const { history } = this.props;
        return (
            <div>
                <nav className={`w3-sidenav w3-collapse sideBar ${isActive(history, "/")} `} style={{ height: window.innerHeight, position: "absolute" }} >
                    <a href="javascript:void(0)" onClick={() => this.w3_close()} className="w3-closenav w3-large w3-hide-large">Close &times;</a>
                    <Link to="/user/Account" className={`w3-bar-item w3-button  ${isActive(history, "/user/Account")}`}>Account</Link>
                    <Link to="/user/DownloadApp" className={`w3-bar-item w3-button  ${isActive(history, "/user/DownloadApp")}`}>Download app</Link>
                    <Link to="/user/Subscription" className={`w3-bar-item w3-button  ${isActive(history, "/user/Subscription")}`}>Subscription</Link>
                    <Link to="/user/BillingCardInfo" className={`w3-bar-item w3-button  ${isActive(history, "/user/BillingCardInfo")} ${isActive(history, "/user/BillingInfo")}`}>Billing</Link>
                    <Link to="/user/PricingPlan" className={`w3-bar-item w3-button  ${isActive(history, "/user/PricingPlan")}`}>Pricing Plan</Link>
                    <Link to="/user/logout" className={`w3-bar-item w3-button  ${isActive(history, "/user/logout")}`}>Logout</Link>
                </nav>

                <div className="w3-main" style={{ marginLeft: "200px" }}>
                    <header>
                        <span className="w3-opennav w3-xlarge w3-hide-large" onClick={() => this.w3_open()}>&#9776;</span>
                    </header>
                </div>
            </div>

            // <div>
            //     <div id="mySidenav" className="sidenav">
            //         <a className="closebtn" onClick={() => this.closeNav()}>&times;</a>
            //         <a href="/user/Account">Account</a>
            //         <a href="/user/DownloadApp">Download app</a>
            //         <a href="/user/Subscription">Subscription</a>
            //         <a href="/user/BillingCardInfo">Billing</a>
            //         <a href="/user/PricingPlan">Pricing Plan</a>
            //         <a href="/user/logout">Logout</a>
            //     </div>
            //     <div className="">
            //         <span className="ml-4" style={{ fontSize: 30, cursor: "pointer", color: "white" }} onClick={() => this.openNav()}>&#9776; </span>
            //         <img className="" src={logo} style={{ width: 130, marginTop: -14 }} />
            //     </div>
            // </div>
        )
    }

}

export const isActive = (history, path) => {
    if (history.location.pathname === path) {
        return "active";
    }
    // console.log (history,path)
};

export default withRouter(SideNav);
