import React from 'react';

import { Route, Switch } from 'react-router-dom';
import Home from './Home/Home';
import SignUp from './SignUp/SignUp'
import Verification from './Verification/Verification'
import BusinessName from './BusinessName/BusinessName';
import BusinessAddress from './BusinessAddress/BusinessAddress';
import Contact from './Contact/Contact'
import PricingPlan from './PricingPlan/PricingPlan'
import PricingPlanUser from './PricingPlan/PricingPlanUser'
import BillingInfo from './BillingInfo/BillingInfo'
import Subcribe from './BillingInfo/Subcribe'
import Header from './Header/Header';
import HeaderTwo from './HeaderTwo/HeaderTwo'
import HeaderThree from "./HeaderThree/HeaderThree";
import SideNav from './SideNav/SideNav'
import Account from './Account/Account'
import DownloadApp from './DownloadApp/DownloadApp';
import Subscription from './Subscription/Subscription';
import BillingCardInfo from './BillingCardInfo/BillingCardInfo';
import BillingCardInfowrong from './BillingCardInfo/BillingCardwrong'
import AccountCreated from './AccountCreated/AccountCreated';
import Login from './Login/Login';
import Logout from './Login/Logout';


const MainRouter = () => (
    <div>
        <Switch>
            <Route path={"/user"}>
                <Header>
                    <Switch>
                        <Route exact path={"/user/Account"} component={Account}></Route>
                        <Route exact path={"/user/DownloadApp"} component={DownloadApp}></Route>
                        <Route exact path={"/user/Subscription"} component={Subscription}></Route>
                        <Route exact path={"/user/BillingCardInfo"} component={BillingCardInfo}></Route>
                        <Route exact path={"/user/BillingCardInfowrong"} component={BillingCardInfowrong}></Route>
                        <Route exact path={"/user/PricingPlan"} component={PricingPlanUser}></Route>
                        <Route exact path={"/user/BillingInfo"} component={Subcribe}></Route>
                        <Route exact path={"/user/logout"} component={Logout}></Route>
                        <Route exact path={"/"} component={SignUp}></Route >
                    </Switch>
                </Header>
            </Route>
            <Route path={"/account"}>
                <HeaderTwo>
                    <Switch>
                        <Route exact path={"/"} component={SignUp}></Route >
                        <Route exact path={"/account/home"} component={Home}></Route >
                        <Route exact path={"/account/login"} component={Login}></Route >
                        <Route exact path={"/account/BusinessName"} component={BusinessName}></Route>
                        <Route exact path={"/account/BusinessAddress"} component={BusinessAddress}></Route>
                        <Route exact path={"/account/Contact"} component={Contact}></Route>
                        <Route exact path={"/account/Verification"} component={Verification}></Route>
                        <Route exact path={"/account/BillingInfo"} component={BillingInfo}></Route>
                        <Route exact path={"/account/PricingPlan"} component={PricingPlan}></Route>
                    </Switch>
                </HeaderTwo>
            </Route>
            <Route path={"/"}>
                <HeaderThree>
                    <Switch>
                        <Route exact path={"/"} component={SignUp}></Route >
                        <Route exact path={"/AccountCreated"} component={AccountCreated}></Route>
                    </Switch>
                </HeaderThree>
            </Route>
        </Switch>
    </div >
);

export default MainRouter;