import React, { Component } from 'react';
import Cookies from "js-cookie";
import { apiCall } from "../Services/Services";
import "./BusinessName.css"
import { Form, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import payment from '../assets/BillingPage.png'
import card from '../assets/Capture9.PNG'
// import Calendar from 'react-input-calendar'
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';
import Loader from "react-loader-spinner";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import "./BusinessName.css"


class BusinessName extends Component {
    constructor(props) {
        super(props);
    }
    state = {
        businessName: "",
        businessAddress: "",
        name: "",
        mobileNumber: "",
        cardNumber: "",
        cvv: "",
        expiry: "",
        step: 1,
        iserror: false,
        errormsg: "no error",
        isloading: false,
        agree: false
    };

    componentDidMount = () => {
        var user_details = Cookies.get("user_details");
        // user_details = JSON.parse(user_details);
        console.log(user_details);

        if (user_details === undefined || user_details == "") {
            this.props.history.push("/");
        }
        user_details = JSON.parse(user_details);
    }

    routeChange = () => {
        let path = `/account/BusinessAddress`;
        this.props.history.push(path);
    }

    handleChange = (event) => {
        if (event.target.id === "name") {
            const name = event.target.value;
            this.setState({ name: name });
        } else if (event.target.id === "mobile") {
            const mobile = event.target.value;
            this.setState({ mobileNumber: mobile });
        } else if (event.target.id === "businessName") {
            const businessName = event.target.value;
            this.setState({ businessName: businessName });
        } else if (event.target.id === "businessAddress") {
            const businessAddress = event.target.value;
            this.setState({ businessAddress: businessAddress });
        } else if (event.target.id === "agree") {
            this.setState({ agree: event.target.checked });
        }
    };

    continue = () => {
        if (this.state.step == 1) {

        }
        this.setState({
            step: this.state.step + 1
        })
    }
    back = () => {
        this.setState({
            step: this.state.step - 1
        })
    }

    complete = () => {
        this.setState({ isloading: true });
        if (this.state.agree) {
            var user_details = Cookies.get("user_details");
            user_details = JSON.parse(user_details);
            console.log('user details', user_details);
            console.log('user ID', user_details.id);
            var action = 'registerBusinessUser';
            var body = JSON.stringify({
                user_id: user_details.id,
                name: this.state.businessName,
                address: this.state.businessAddress,
                incharge_name: this.state.name,
                incharge_mobile_number: this.state.mobileNumber
            });
            apiCall(action, body).then((result) => {
                console.log("register", result)
                let responseJSON = result;
                if (responseJSON.error) {
                    this.setState({
                        iserror: true,
                        errormsg: responseJSON.message,
                    });
                } else {
                    if (responseJSON.data) {
                        console.log("user register respone", responseJSON.data)
                        localStorage.setItem("user_registered", responseJSON.data);
                        Cookies.set("user_registered", responseJSON.data, { expires: 1 });
                        let path = `/account/BillingInfo`;
                        this.props.history.push(path);
                    }
                }
                this.setState({ isloading: false });
            })
        }
        else {
            this.setState({
                iserror: true,
                errormsg: "Accept the agreement first",
                isloading: false
            });
        }

    }



    render() {
        const { iserror, errormsg, isloading } = this.state;
        if (this.state.step === 1) {
            return (
                <div className="container">
                    <div className="row mt-5 ml-3 mr-3">

                        <div className="col-lg-12 col-sm-12">
                            <div className="col">
                                <div className="row">
                                    <div className="col-12">
                                        <h4 style={{ fontWeight: "bold" }}>Business name</h4>
                                    </div>
                                </div>
                                <Form className="mt-5">
                                    <Form.Group controlId="businessName">
                                        <Form.Control style={{
                                            padding: "0px",
                                            fontSize: "20px",
                                            borderRight: "none",
                                            borderLeft: "none",
                                            borderTop: "none",
                                            borderRadius: "0px",
                                            borderBottomWidth: "2px",
                                            borderBottomColor: "black",
                                            boxShadow: "none"
                                        }} type="name" placeholder="business name" name="businessName" value={this.state.businessName} onChange={this.handleChange} required />
                                    </Form.Group>
                                    <Button onClick={this.continue} className="btn" style={{ background: "#2101F0", color: "white", width: "100%", marginTop: 80 }} >
                                        Continue
                                </Button>
                                </Form>
                            </div>
                        </div>

                    </div>
                </div>

            )
        } else if (this.state.step === 2) {
            return (
                <div className="container">
                    <div className="row mt-5 ml-3 mr-3">

                        <div className="col-lg-12 col-sm-12">
                            <div className="col">
                                <div className="row">
                                    <div className="col-12">
                                        <Link onClick={this.back} style={{ fontSize: "32px", color: "black" }}>&#8249;</Link><h4 style={{ fontWeight: "bold" }}>Business address</h4>
                                    </div>
                                </div>
                                <Form className="mt-5">
                                    <Form.Group controlId="businessAddress">
                                        <Form.Control style={{
                                            padding: "0px",
                                            fontSize: "20px",
                                            borderRight: "none",
                                            borderLeft: "none",
                                            borderTop: "none",
                                            borderRadius: "0px",
                                            borderBottomWidth: "2px",
                                            borderBottomColor: "black",
                                            boxShadow: "none"
                                        }} as="textarea" rows="2" type="name" placeholder="business address" required name="businessAddress" value={this.state.businessAddress} onChange={this.handleChange} />
                                    </Form.Group>
                                    <Button onClick={this.continue} className="btn" style={{ background: "#2101F0", color: "white", width: "100%", marginTop: 80 }} >
                                        Continue
                                </Button>
                                </Form>
                            </div>
                        </div>

                    </div>
                </div>
            )
        } else if (this.state.step === 3) {
            return (
                <div className="container">
                    <div className="row mt-5 ml-3 mr-3">

                        <div className="col-lg-12 col-sm-12">
                            <div className="col">
                                <div className="row">
                                    <div className="col-12">
                                        <Link onClick={this.back} style={{ fontSize: "32px", color: "black" }}>&#8249;</Link><h4 style={{ fontWeight: "bold" }}>Person in-charge contact</h4>
                                    </div>
                                </div>
                                <Form className="mt-5">
                                    <Form.Group controlId="name">
                                        <Form.Control style={{
                                            padding: "0px",
                                            fontSize: "20px",
                                            borderRight: "none",
                                            borderLeft: "none",
                                            borderTop: "none",
                                            borderRadius: "0px",
                                            borderBottomWidth: "2px",
                                            borderBottomColor: "black",
                                            boxShadow: "none"
                                        }} type="name" placeholder="name" required name="name" value={this.state.name} onChange={this.handleChange} />
                                    </Form.Group>
                                    <Form.Group className="mt-5" controlId="mobile">
                                        <Form.Control style={{
                                            padding: "0px",
                                            fontSize: "20px",
                                            borderRight: "none",
                                            borderLeft: "none",
                                            borderTop: "none",
                                            borderRadius: "0px",
                                            borderBottomWidth: "2px",
                                            borderBottomColor: "black",
                                            boxShadow: "none"
                                        }} type="number" placeholder="mobile number" required name="mobileNumber" value={this.state.mobileNumber} onChange={this.handleChange} />
                                    </Form.Group>
                                    <Button onClick={this.continue} className="btn" style={{ background: "#2101F0", color: "white", width: "100%", marginTop: 80 }} >
                                        Continue
                                </Button>
                                </Form>
                            </div>
                        </div>

                    </div>
                </div>
            )
        } else if (this.state.step === 4) {
            return (
                <div className="container">
                    <div className="row  mt-5 ml-2 mr-2">

                        <div className="col-lg-12 col-sm-12">
                            <div className="row">
                                <div className="col-12">
                                    <h4 style={{ fontWeight: "bold" }}>Pricing Plan</h4>
                                </div>
                            </div>
                            <div className="row mt-4">
                                <div className="col-12">
                                    <div style={{ fontSize: 20 }}><span style={{ fontWeight: "bold", fontSize: 18, marginBottom: 0 }}>Free</span> <span style={{ fontSize: 14, fontWeight: "500" }}>- RM</span> 0 monthly</div>
                                    <span style={{ fontSize: 20, color: "gray" }}>0 - 100 customer base</span>
                                </div>
                            </div>
                            <div className="row mt-4">
                                <div className="col-12">
                                    <div style={{ fontSize: 20 }}><span style={{ fontWeight: "bold", fontSize: 18, marginBottom: 0 }}>Essentials</span> <span style={{ fontSize: 14, fontWeight: "500" }}>- RM</span> 0 monthly</div>
                                    <span style={{ fontSize: 20, color: "gray" }}>101 - 500 customer base</span>
                                </div>
                            </div>
                            <div className="row mt-4">
                                <div className="col-12">
                                    <div style={{ fontSize: 20 }}><span style={{ fontWeight: "bold", fontSize: 18, marginBottom: 0 }}>Standard</span> <span style={{ fontSize: 14, fontWeight: "500" }}>- RM</span> 0 monthly</div>
                                    <span style={{ fontSize: 20, color: "gray" }}>501 - 1k customer base</span>
                                </div>
                            </div>
                            <div className="row mt-4">
                                <div className="col-12">
                                    <div style={{ fontSize: 20 }}><span style={{ fontWeight: "bold", fontSize: 18, marginBottom: 0 }}>Premium</span> <span style={{ fontSize: 14, fontWeight: "500" }}>- RM</span> 0 monthly</div>
                                    <span style={{ fontSize: 20, color: "gray" }}>1k - unlimited customers</span>
                                </div>
                            </div>
                            <div className="row mt-5">
                                <div className="col">
                                    <h5 style={{ color: "gray" }}>Terms of service</h5>
                                    <hr style={{ marginRight: "44%", marginTop: 0, marginBottom: 0, borderColor: "black" }} />
                                </div>
                            </div>
                            <div className="row mt-0">
                                <div className="col-12">
                                    <input style={{ zoom: 1.3, fontSize: "12px" }} type="checkbox" class="hidden" id="agree" readonly="" onChange={this.handleChange} tabindex="0" />
                                    <label className="pl-2" style={{ fontSize: 12, color: "gray" }}>I have acknowledge to agree above all <br />pricing terms to utilise the platform</label>
                                </div>
                            </div>
                            <div className="row mt-1">
                                <div className="col-12 buttons">
                                    <button onClick={this.complete} className="btn " style={{ background: "#2101F0", color: "white", width: "240px" }} >
                                        I Agree
                                </button>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            )
        } else if (this.state.step === 5) {
            return (
                <div className="container">
                    <div className="row  mt-5 ml-3 mr-3">

                        <div className="col-lg-4 col-sm-12">
                            <div className="row">
                                <div className="col-12">
                                    <h4 style={{ fontWeight: "bold" }}><Link onClick={this.back} style={{ fontSize: "32px", color: "black" }}>&#8249;</Link> Billing info</h4>
                                </div>
                            </div>
                            <div className="row mt-2">
                                <div className="col-3">
                                    <img src={card} style={{ width: 60 }} />
                                </div>
                                <div className="col-3"></div>
                                <div className="col-6">
                                    <img src={payment} style={{ width: 130 }} />
                                </div>

                            </div>
                            {/* <div className="container">
                                    <div className="mt-5">
                                        <span style={{ fontSize: "18px", color: "gray" }}>Card number</span >
                                        <input style={{ width: "100%", borderRadius: "10px", borderColor: "gray" }} type="number" name="cardNumber" id="search" onChange={this.handleChange} />
                                    </div>
                                </div> */}
                            <Form className="mt-5">
                                <Form.Group controlId="cardNumber">
                                    <span style={{ fontSize: "14px", color: "gray" }}>Card number</span >
                                    <Form.Control style={{ fontSize: "16px", borderRadius: "10px" }} type="number" required name="cardNumber" onChange={this.handleChange} />
                                </Form.Group>
                                {/* <Form.Group className="container" controlId="cvv"> */}
                                <div className="row container">
                                    <span style={{ fontSize: "14px", color: "gray" }}>CVV</span >
                                    <span style={{ fontSize: "14px", color: "gray", marginLeft: 95 }}>Expiry</span >
                                </div>
                                {/* </Form.Group> */}
                                <Form.Group className="container" controlId="expiry">
                                    <div className="row">
                                        <Form.Control style={{ fontSize: "16px", borderRadius: "10px", width: "35%" }} type="name" required name="cvv" onChange={this.handleChange} />
                                        <Form.Control style={{ fontSize: "16px", borderRadius: "10px", width: "55%", marginLeft: 20 }} type="month" required name="expiry" onChange={this.handleChange} />
                                        {/* <Calendar format='DD/MM/YYYY' date='4-12-2014' /> */}
                                    </div>
                                </Form.Group>
                            </Form>
                            {/* <div className="container">
                                <div className=" mt-2">
                                    <span style={{ fontSize: "18px", color: "gray" }}>CVV</span >
                                    <span style={{ fontSize: "18px", color: "gray", marginLeft: 90 }}>Expiry</span >
                                </div>
                                <div className=" mt-2 mb-5">
                                    <input style={{ width: "40%", borderRadius: "10px", borderColor: "gray", }} type="text" name="CVV" onChange={this.handleChange} />
                                    <input style={{ width: "50%", borderRadius: "10px", borderColor: "gray", marginLeft: 20 }} name="expiry" type="number" placeholder="mm/yy" onChange={this.handleChange} />
                                </div>
                            </div> */}
                            <div className="pt-5">
                                <p style={{ fontSize: "14px" }}>It will automactically upgrade to Essentials
                                Plan when you exceed 100 customer base.
                            Flexible, no contract. Unsubscribe anytime.</p>
                            </div>
                            {iserror ? <p className="text-danger text-center">{errormsg}</p> : ""}
                            {isloading ? (
                                <Loader className="text-center" type="Oval" color="#323A40" height={50}
                                    width={50}
                                // timeout={3000} //3 secs
                                />
                            ) : (
                                    <div className=" text-center buttons">
                                        <button onClick={this.complete} className="btn " style={{ background: "#2101F0", color: "white", width: "100%", fontSize: "18px", borderRadius: "10px" }} >
                                            Complete
                                    </button>
                                    </div>
                                )}

                        </div>

                    </div>
                </div>
            )
        }
    }
}
export default BusinessName;