import React, { Component } from 'react';
// import "./BusinessName.css"
import Cookies from "js-cookie";
import { apiCall } from "../Services/Services";
import Loader from "react-loader-spinner";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Footer from "../Footer/Footer";


class Pricingplan extends Component {
    constructor(props) {
        super(props);
    }
    state = {
        userData: null,
        iserror: false,
        errormsg: "no error",
        isloading: false,
        agree: false
    }
    componentDidMount = () => {
        var user_details = Cookies.get("user_details");
        // user_details = JSON.parse(user_details);
        console.log(user_details);

        if (user_details === undefined || user_details == "") {
            this.props.history.push("/");
        }
        user_details = JSON.parse(user_details);

        var action = 'getUserData';
        var body = JSON.stringify({
            id: user_details.id === undefined ? user_details.user.id : user_details.id
        });
        apiCall(action, body).then((result) => {
            console.log("getUserData", result)
            let responseJSON = result;
            if (responseJSON.error) {
                this.props.history.push("/");
            } else {
                if (responseJSON.data) {
                    console.log("dataa", responseJSON.data);
                    this.setState({ userData: responseJSON.data });
                }
            }
        })
    }
    routeChange = () => {
        console.log(this.state.userData);
        this.setState({ isloading: true });
        if (this.state.agree) {
            if (this.state.userData == null)
                var path = `/account/BillingInfo`;
            else
                var path = `/user/BillingInfo`;
            this.props.history.push(path);
        } else {
            this.setState({ iserror: true });
            this.setState({ errormsg: "Agree Terms of Services" });
            this.setState({ isloading: false });
        }
    }
    handleChange = (event) => {
        console.log(event.target.checked);
        this.setState({ agree: event.target.checked });
    }
    render() {
        const { iserror, errormsg, isloading } = this.state;
        return (
            <div className="container">
                <div className="row  mt-5 ml-3 mr-3">
                    <div className="col-lg-4 col-sm-12"></div>
                    <div className="col-lg-12 col-sm-12">
                        <div className="row">
                            <div className="col-12">
                                <h4 style={{ fontWeight: "bold" }}>Pricing plan</h4>
                            </div>
                        </div>
                        <div className="row mt-4">
                            <div className="col-12">
                                <div style={{ fontSize: 20 }}><span style={{ fontWeight: "bold", fontSize: 22, marginBottom: 0 }}>Free</span> <span style={{ fontSize: 14 }}>- RM</span> 0 monthly</div>
                                <span style={{ fontSize: 20, color: "gray" }}>0 - 100 customer base</span>
                            </div>
                        </div>
                        <div className="row mt-4">
                            <div className="col-12">
                                <div style={{ fontSize: 20 }}><span style={{ fontWeight: "bold", fontSize: 22, marginBottom: 0 }}>Essentials</span> <span style={{ fontSize: 14 }}>- RM</span> 0 monthly</div>
                                <span style={{ fontSize: 20, color: "gray" }}>101 - 500 customer base</span>
                            </div>
                        </div>
                        <div className="row mt-4">
                            <div className="col-12">
                                <div style={{ fontSize: 20 }}><span style={{ fontWeight: "bold", fontSize: 22, marginBottom: 0 }}>Standard</span> <span style={{ fontSize: 14 }}>- RM</span> 0 monthly</div>
                                <span style={{ fontSize: 20, color: "gray" }}>501 - 1k customer base</span>
                            </div>
                        </div>
                        <div className="row mt-4">
                            <div className="col-12">
                                <div style={{ fontSize: 20 }}><span style={{ fontWeight: "bold", fontSize: 22, marginBottom: 0 }}>Premium</span> <span style={{ fontSize: 14 }}>- RM</span> 0 monthly</div>
                                <span style={{ fontSize: 20, color: "gray" }}>1k - unlimited customers</span>
                            </div>
                        </div>
                        <div className="row mt-5">
                            <div className="col-12">
                                <h5 style={{ color: "gray" }}>Terms of service</h5>
                                <hr style={{ marginRight: "44%", margin: "0%" }} />
                            </div>
                        </div>
                        <div className="row mt-1">
                            <div className="col-12">
                                <input style={{ zoom: 1.5 }} type="checkbox" id="agree" value="1" class="hidden" onClick={this.handleChange} readonly="" tabindex="0" />
                                <label className="pl-2" style={{ fontSize: 12, color: "gray" }}>I have acknowledge to agree above all <br />pricing terms to utilise the platform</label>
                            </div>
                        </div>
                        {iserror ? <p className="text-danger text-center">{errormsg}</p> : ""}
                        <div className="row mt-4">
                            <div className="col-12 text-center buttons">
                                {isloading ? (
                                    <Loader className="text-center" type="Oval" color="#323A40" height={50}
                                        width={50}
                                    // timeout={3000} //3 secs
                                    />
                                ) : (
                                        <button onClick={this.routeChange} className="btn " style={{ background: "#2101F0", color: "white", width: "200px" }} >
                                            I Agree
                                        </button>


                                    )}
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-4 col-sm-12"></div>
                </div>
                {/* <div style={{ marginTop: 140 }}>
                    <Footer />
                </div> */}
            </div>
        )
    }
}
export default Pricingplan;