import React, { Component } from 'react';
import { Link } from 'react-router-dom'
import Cookies from "js-cookie";

// import "./BusinessName.css"


class BusinessAddress extends Component {
    constructor(props) {
        super(props);
    }
    componentDidMount = () => {
        var user_details = Cookies.get("user_details");
        // user_details = JSON.parse(user_details);
        console.log(user_details);

        if (user_details === undefined || user_details == "") {
            this.props.history.push("/");
        }
        user_details = JSON.parse(user_details);
    }
    routeChange = () => {
        let path = `/account/Contact`;
        this.props.history.push(path);
    }
    render() {
        return (
            <div className="container">
                <div className="row mt-5 ml-3 mr-3">
                    <div className="col-lg-12 col-sm-12">
                        <div className="col">
                            <div className="row">
                                <div className="col-12">
                                    <Link style={{ fontSize: "32px", color: "black" }}>&#8249;</Link> <h4 style={{ fontWeight: "bold" }}> Business Address</h4>
                                </div>
                            </div>
                            <div className="row mt-5">
                                <div className="col-12">
                                    <input type="name" placeholder="business address" style={{ borderRight: "none", borderLeft: "none", borderTop: "none", width: "100%" }} /><br /><br />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-12 text-center buttons" style={{ marginTop: "30%" }}>
                                    <button onClick={this.routeChange} className="btn " style={{ background: "#2101F0", color: "white", width: "100%" }} >
                                        Continue
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        )
    }
}
export default BusinessAddress;