import { parseJSON } from 'jquery';
import React, { Component } from 'react';
import { apiCall } from "../Services/Services";
import { Form, Button } from "react-bootstrap";
import Cookies from "js-cookie";
import Loader from "react-loader-spinner";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";




class Home extends Component {
    constructor(props) {
        super(props);
    }
    state = {
        email: "",
        password: "",
        iserror: false,
        errormsg: "no error",
        isloading: false,
    };
    componentDidMount = () => {
        // var user_details = Cookies.get("user_details");
        // // user_details = JSON.parse(user_details);
        // console.log(user_details);

        // if (user_details === undefined || user_details == "") {

        // }
        // else {
        //     this.props.history.push("/user/Account");
        // }

    }
    // routeChange = () => {
    //     let path = `/Verification`;
    //     this.props.history.push(path);
    // }
    signup = () => {
        this.setState({ isloading: true });
        var action = 'signup';
        var body = JSON.stringify({
            email: this.state.email,
            password: this.state.password,
            type: "business"
        });
        Cookies.set("user_email", this.state.email, { expires: 1 });
        Cookies.set("user_password", this.state.password, { expires: 1 });

        apiCall(action, body).then((result) => {
            console.log("signup", result)
            let responseJSON = result;
            if (responseJSON.error) {
                this.setState({
                    iserror: true,
                    errormsg: responseJSON.message,
                });
            } else {
                if (responseJSON.data) {
                    console.log("dataa", responseJSON.data)
                    localStorage.setItem("user_details", responseJSON.data);
                    Cookies.set("user_details", responseJSON.data, { expires: 1 });
                    this.props.history.push("/account/Verification");
                }
            }
            this.setState({ isloading: false });
        })
    };

    handleChange = (e) => {
        if (e.target.id === "id") {
            const email = e.target.value;
            this.setState({ email: email });
        } else if (e.target.id === "pass") {
            const pass = e.target.value;
            this.setState({ password: pass });
        }
    };

    render() {
        const { iserror, errormsg, isloading } = this.state;

        return (
            <div>
                <div className="container">
                    <div className="row mt-5 ml-3 mr-3">

                        <div className="col-lg-12 col-sm-12">
                            <div className="col">
                                <div className="row">
                                    <div className="col-12">
                                        <h4 style={{ fontWeight: "bold" }}>Welcome</h4>
                                    </div>
                                </div>
                                <div className="row mt-3">
                                    <div className="col-12">
                                        <p style={{}}>Sign up with your email </p>
                                    </div>
                                </div>

                                <Form className="mt-5">
                                    <Form.Group controlId="id">
                                        <Form.Control style={{ padding: "0px", fontSize: "20px", borderRight: "none", borderLeft: "none", borderTop: "none", borderRadius: "0px", borderBottomWidth: "2px", borderBottomColor: "black", boxShadow: "none" }} type="email" placeholder="Email" required name="email" onChange={this.handleChange} />
                                    </Form.Group>
                                    <Form.Group controlId="pass">
                                        <Form.Control style={{ padding: "0px", fontSize: "20px", borderRight: "none", borderLeft: "none", borderTop: "none", borderRadius: "0px", borderBottomWidth: "2px", borderBottomColor: "black", boxShadow: "none" }} type="password" placeholder="password" required name="password" onChange={this.handleChange} />
                                    </Form.Group>
                                    {iserror ? <p className="text-danger text-center">{errormsg}</p> : ""}
                                    {isloading ? (
                                        <Loader className="text-center" type="Oval" color="#323A40" height={50}
                                            width={50}
                                        />
                                    ) : (
                                            <Button onClick={this.signup} className="btn" style={{ background: "#2101F0", color: "white", width: "100%", marginTop: 80 }} >
                                                Continue
                                            </Button>
                                        )}
                                </Form>
                                {/* <div className="row">
                                    <div className="col-12 text-center buttons" style={{ marginTop: "40%" }}>
                                        <button onClick={this.signup} className="btn " style={{ background: "#2101F0", color: "white", width: "100%" }} >
                                            Continue
                                        </button>
                                    </div>
                                </div> */}
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        )
    }
}
export default Home;