const BaseUrl = "https://api.socard.io/api/auth/";

export function apiCall(action, body, headers = {
                "Content-Type": "application/json",
                "X-Requested-With": "XMLHttpRequest"
            }) {
    return new Promise((resolve, reject) => {
        // console.log(empData);
        fetch(BaseUrl + action, {
            method: "POST",
            body: body,
            headers: headers,
        })
            .then((response) => response.json())
            .then((responseJson) => {
                // console.log("api response from login", responseJson)
                resolve(responseJson);
            })
            .catch((error) => {
                console.log(error);
                reject(error);
            });
    });
}