import React, { Component } from 'react';
import Cookies from "js-cookie";
import card from '../assets/card.png';
import card2 from '../assets/2.png';
import { apiCall } from "../Services/Services";
import Loader from "react-loader-spinner";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Footer from "../Footer/Footer";

class Subscription extends Component {

    constructor(props) {
        super(props);
    }
    state = {
        userData: null,
        iserror: false,
        errormsg: "no error",
        isloading: false,
    }
    componentDidMount = () => {
        var user_details = Cookies.get("user_details");
        // user_details = JSON.parse(user_details);
        console.log("user detials on subctription page", user_details);

        if (user_details === undefined || user_details == "") {
            this.props.history.push("/");
        }
        user_details = JSON.parse(user_details);

        var action = 'getUserData';
        var body = JSON.stringify({
            id: user_details.id === undefined ? user_details.user.id : user_details.id
        });
        apiCall(action, body, {
            "Content-Type": "application/json",
            "X-Requested-With": "XMLHttpRequest",
            'Authorization': 'Bearer ' + user_details.token === undefined ? user_details.user.token : user_details.token,
        }).then((result) => {
            console.log("getUserData", result)
            let responseJSON = result;
            if (responseJSON.error) {
                this.props.history.push("/");
            } else {
                if (responseJSON.data) {
                    console.log("dataa", responseJSON.data);
                    this.setState({ userData: responseJSON.data });
                    // if(responseJSON.data.subscription == 0) this.props.history.push("/user/PricingPlan");
                }
            }
        });
    }

    continue = () => {
        let path = `/user/PricingPlan`;
        this.props.history.push(path);
    }
    unSubscribe = () => {
        this.setState({ isloading: true });
        var user_details = Cookies.get("user_details");
        user_details = JSON.parse(user_details);

        var action = 'subscription';
        var body = JSON.stringify({
            user_id: user_details.id === undefined ? user_details.user.id : user_details.id,
            status: 0
        });
        apiCall(action, body, {
            "Content-Type": "application/json",
            "X-Requested-With": "XMLHttpRequest",
            'Authorization': 'Bearer ' + this.state.userData.user.token,
        }).then((result) => {
            console.log("subscription", result)
            let responseJSON = result;
            if (responseJSON.error) {
                this.setState({
                    iserror: true,
                    errormsg: responseJSON.message,
                });
            } else {
                if (responseJSON.data) {
                    console.log("dataa", responseJSON.data);
                    let path = `/user/Subscription`;
                    window.location.reload(false);
                    this.props.history.push(path);
                }
            }

            this.setState({ isloading: false });
        });


    }

    getPlanName = (base) => {
        if (base <= 100) return "Free";
        else if (base <= 500) return "Essentials";
        else if (base <= 1000) return "Standard";
        else return "Premium";
    }

    getPlanRM = (base) => {
        if (base <= 100) return "0";
        else if (base <= 500) return "65";
        else if (base <= 1000) return "150";
        else return "250";
    }

    render() {
        const { iserror, errormsg, isloading, userData } = this.state;
        console.log("User Data", userData);
        return (
            <div className="container">
                <div className="row">
                    <div className="col-lg-4 col-sm-12"></div>
                    <div className="col-lg-12 col-sm-12">
                        <div className="ml-5 mr-5 mt-5">
                            <div>
                                <h3 style={{ fontWeight: "bold" }}>Subscription</h3>
                            </div>
                            <div className="mt-4">
                                <span style={{ fontWeight: "bold", fontSize: "18px" }}>{userData == null || userData.customer_base == null || userData.customer_base === undefined ? "Free" : this.getPlanName(userData.customer_base)} Plan</span>
                                <p style={{ fontSize: "16px" }}>RM {userData == null || userData.customer_base == null || userData.customer_base === undefined ? "0" : this.getPlanRM(userData.customer_base)} Monthly</p>
                            </div>
                            <div className="mt-4">
                                <h3 style={{ fontWeight: "bold" }}>{userData == null || userData.customer_base == null || userData.customer_base === undefined ? 0 : userData.customer_base}</h3>
                                <p style={{ fontSize: "16px" }}>Customer base</p>
                            </div>
                            <div className="mt-4">
                                <h3 style={{ fontWeight: "bold" }}>{userData == null || userData.points == null || userData.points === undefined ? 0 : userData.points}</h3>
                                <p style={{ fontSize: "16px" }}>Total customer points</p>
                            </div>
                            <div className="mt-4">
                                {userData == null || userData.subscription == 0 || userData.is_pause ? <p className="text-danger" style={{ fontWeight: "bold" }}>N/A</p> : <h6 style={{ fontWeight: "bold" }}>25 October 2020</h6 >}
                                <p style={{ fontSize: "16px" }}>Your next billing date</p>
                                {userData == null || userData.subscription == 0 ? <p className="text-danger">All of your service will be terminated on {userData == null || userData.next_billing_date === undefined ? "(Loading...)" : userData.next_billing_date}</p> : ""}

                            </div>
                            <div className="mt-5">
                                {userData == null || userData.subscription == 0 || userData.is_pause ? <img src={card2} style={{ width: 80 }} /> : <img src={card} style={{ width: 80 }} />}

                            </div>
                            {userData == null || userData.is_pause ? <p className="text-danger" style={{ fontWeight: "bold" }}>Account Paddused</p> : ""}
                            <div className="mt-3">
                                <h6>Your subscription is processed by
                                payment processor <span style={{ fontWeight: "bold" }}>Stripe</span>.</h6>
                            </div>
                            {iserror ? <p className="text-danger">{errormsg}</p> : ""}
                            <div className="mt-4">
                                {isloading ? (
                                    <Loader className="ml-5" type="Oval" color="#323A40" height={50}
                                        width={50}
                                    // timeout={3000} //3 secs
                                    />
                                ) : (
                                        userData == null || userData.subscription == 0 || userData.is_pause ? (
                                            <button onClick={this.continue} className="btn btn-primary" style={{ width: "200px", backgroundColor: "#2101F0" }} >
                                                Continue
                                            </button>
                                        )
                                            : (
                                                <button onClick={this.unSubscribe} className="btn" style={{ background: "#E6E6E6", color: "red", width: "200px" }} >
                                                    Unsubscribe
                                                </button>
                                            )

                                    )}
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-lg-4 col-sm-12"></div>
                {/* <div style={{ marginTop: 140 }}>
                    <Footer />
                </div> */}
            </div>
        )
    }


}

export default Subscription;
