import React, { Component } from 'react';
import round from '../assets/SoConnect_ROUNDED.png'
import appstore from '../assets/AppStore.png'
import right from '../assets/right-mark.png'
import Cookies from "js-cookie";
import './AccountCreated.css'
import Footer from '../Footer/Footer'

class AccountCreated extends Component {
    constructor(props) {
        super(props);
    }
    componentDidMount = () => {
        var user_details = Cookies.get("user_details");
        // user_details = JSON.parse(user_details);
        console.log(user_details);

        if (user_details === undefined || user_details == "") {
            this.props.history.push("/");
        }
        else {
            user_details = JSON.parse(user_details);
        }

    }
    routeChange = () => {
        let path = `/user/Account`;
        this.props.history.push(path);
    }

    render() {
        return (
            <div className="container-fluid p-0">
                <div className="row bg-img2">
                    <div className="col-lg-12 col-sm-12">
                        <div className="container-fluid pad-for">
                            <div className="row mt-5">
                                <div className="col-lg-4 col-sm-0 text-center" ></div>
                                <div className="col-lg-4 col-sm-12 text-center" style={{ marginTop: '3%' }}>
                                    <h2 style={{ fontWeight: "bold", color: "white" }}>Account Created <img src={right} width="30px" height="30px" /></h2>
                                </div>
                                <div className="col-lg-4 col-sm-0 text-center" ></div>
                            </div>

                            <div className="row mt-4">
                                <div className="col-lg-4 col-sm-0 text-center" style={{ marginTop: 0 }}></div>
                                <div className="col-lg-4 col-sm-12 text-center" style={{ marginTop: 0 }}>
                                    <div className="mt-3 mb-3">
                                        <p className="parah">Youve successfully created your account. Download <b>SoConnect</b> app and start access your account.</p>
                                    </div>
                                    <div>
                                        <img className="yar" src={round} width="100px" />
                                    </div>
                                    <div className="mt-3 mb-3">
                                        <p style={{ fontSize: "25px", fontWeight: "bold" }}>SoConnect</p>
                                    </div>
                                    <div>
                                        <img src={appstore} width="200px" />
                                    </div>
                                </div>
                                <div className="col-lg-4 col-sm-0 text-center" style={{ marginTop: 0 }}></div>
                            </div>
                            <div className="row mt-5 lftright">
                                <div className="col-lg-4 col-sm-12"></div>
                                <div className="col-lg-4 col-sm-12">
                                    <p style={{ fontSize: "16px", textAlign: "center" }}>
                                        You can manage your subscription
                                        and billing info in your account.
                                </p>
                                </div>
                                <div className="col-lg-4 col-sm-12"></div>
                            </div>
                            <div className="row lftright">
                                <div className="col-lg-4 col-sm-12"></div>
                                <div className="col-lg-4 col-sm-12 text-center buttons">
                                    <button onClick={this.routeChange} className="btn " style={{ background: "#2101F0", color: "white", width: "100%" }} >
                                        Go to account
                                    </button>
                                </div>
                                <div className="col-lg-4 col-sm-12"></div>
                            </div>
                        </div>
                    </div>
                </div>
                {/* <div style={{ marginTop: 140 }}>
                    <Footer />
                </div> */}
            </div>
        )
    }
}

export default AccountCreated;
